.navigation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
}

.navigation_links_bar {
  background-color: #3a3c40;
  height: 35px;
}

.navigation_name_bar {
  background-color: #222735;
  height: 60px;
  margin-left: 0;
}

.navigation_name_bar span {
  color: #fff;
  font-size: 48px;
  margin-left: 8.3%;
}

#links_result {
  display: flex;
  justify-content: flex-end;
}

#links_result a {
  text-decoration: none;
  color: #fff;
  margin-left: 20px;
}

#links_result a:last-of-type {
  margin-right: 8.3%;
}

#footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

#footer_above {
  height: 5px;
  width: 100%;
  background-color: #3a3c40;
}

#footer_bottom {
  height: 30px;
  width: 100%;
  background-color: #222735;
  display: flex;
  justify-content: space-around;
}

#footer_bottom .comment {
  color: #fff;
  size: 2px;
}

#content_container {
  margin-top: 95px;
  height: 100%;
}

#projects_result {
  display: grid;
  grid-template-columns: 3fr 16px 3fr 16px 3fr 16px 3fr;
  grid-template-rows: 1fr;
}

#projects_result .project {
  width: 2fr;
  height: 400px;
  background: #fff;
  border: solid 2px #3a3c40;
  border-radius: 10px;
  margin-top: 20px;
  overflow: hidden;
  box-shadow: 3px 6px #aaaaaa 10px;
}

#projects_result .project img {
  height: 320px;
  width: 100%;
  object-fit: cover;
}

#projects_result .project .project_text {
  height: 60px;
  position: relative;
  float: left;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#projects_result .project:nth-child(4n+1) {
  grid-column: 1 / 2;
}

#projects_result .project:nth-child(4n+2) {
  grid-column: 3 / 4;
}

#projects_result .project:nth-child(4n+3) {
  grid-column: 5 / 6;
}

#projects_result .project:nth-child(4n+4) {
  grid-column: 7 / 8;
}

grid-overlay {
  position: absolute;
  left: 0;
  top: 0;
}

.list_With_Line {
  margin-top: -20px;
  padding: 15px;
  text-align: justify;
}

.list_With_Line:nth-child(even) {
  border-top: solid 4px #006eff;
  border-left: solid 4px #006eff;
  border-bottom: solid 4px #006eff;
  border-radius: 30px 0 0 30px;
  margin-right: 30px;
}

.list_With_Line:nth-child(odd) {
  border-top: solid 4px #006eff;
  border-right: solid 4px #006eff;
  border-bottom: solid 4px #006eff;
  border-radius: 0 30px 30px 0;
  margin-left: 30px;
}

/*
essential styles:
these make the slideshow work
*/
#slides {
  position: relative;
  height: 300px;
  padding: 0px;
  margin: 0px;
  list-style-type: none;
}

.slide {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}

.showing {
  opacity: 1;
  z-index: 2;
}

/*
non-essential styles:
just for appearance; change whatever you want
*/
.slide {
  font-size: 40px;
  padding: 40px;
  box-sizing: border-box;
  background: #333;
  color: #fff;
}

.slide:nth-of-type(1) {
  background: red;
}

.slide:nth-of-type(2) {
  background: orange;
}

.slide:nth-of-type(3) {
  background: green;
}

.slide:nth-of-type(4) {
  background: blue;
}

.slide:nth-of-type(5) {
  background: purple;
}
